___CSS_LOADER_EXPORT___.push([module.id, `.custom-marker__container {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  line-height: 1.35;
  letter-spacing: 0.6px;
  color: white;
  position: relative;
  box-sizing: border-box;
  top: -50%;
}

.custom-marker__point .custom-marker__bg-pointer {
  content: "";
  box-shadow: -2px -2px 2px 0px rgb(0 0 0 / 8%);
  border-radius: 2px;
  position: absolute;
  bottom: -3px;
  left: 50%;
  /* width: 16px;
  height: 16px; */
  width: 26px;
  height: 26px;
  transform: translateX(-50%) rotate(45deg);
  transform-origin: center;
  z-index: 1;
}

.custom-marker__bg,
.custom-marker__container {
  min-width: 100%;
  min-height: 100%;
}

.custom-marker__bg {
  border-radius: 50px;
}

.custom-marker .bg-color {
  background-color: #5e606f;
  opacity: 0.7;
}

.custom-marker__bg,
.custom-marker__content {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}

.custom-marker__point {
  width: 100%;
  height: 100%;
  padding: 3px;
  z-index: 2;
  position: absolute;
  display: flex;
  left: 0px;
  top: 0px;
  border-color: unset;
  border: 0px;
  border: 1px;
  border-radius: 3px;
  white-space: nowrap;
  -webkit-transition: background-color 250ms linear, transform 250ms;
  -moz-transition: background-color 250ms linear, transform 250ms;
  -o-transition: background-color 250ms linear, transform 250ms;
  -ms-transition: background-color 250ms linear, transform 250ms;
  transition: background-color 250ms linear, transform 250ms
}

.custom-marker__point .image-container {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}

.custom-marker__point img {
  width: 100% !important;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.custom-marker__point .text-container {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  color: white;
  z-index: 1;
  padding-right: 8px;
}

/* HOVER */
.custom-marker:has(.custom-marker__container:hover) {
  z-index: 99999 !important;
}
.custom-marker__container:hover {
  -webkit-transition: background-color 250ms linear, transform 250ms;
  -moz-transition: background-color 250ms linear, transform 250ms;
  -o-transition: background-color 250ms linear, transform 250ms;
  -ms-transition: background-color 250ms linear, transform 250ms;
  transition: background-color 250ms linear, transform 250ms;
  -ms-transform: scale(2);
  -webkit-transform: scale(2);
  transform: scale(2)
}

.custom-marker__container:hover .custom-marker__point {
  height: auto;
  width: auto;
  gap: 8px;
}

.custom-marker__container:hover .custom-marker__bg {
  opacity: .9;
}

.custom-marker__container:hover .custom-marker__bg-pointer {
  display: none;
}

.custom-marker__container:hover .custom-marker__point .image-container {
  width: 44px !important;
  height: 44px !important;
}

.custom-marker__container:hover .custom-marker__point .text-container {
  display: flex;
}`, "",{"version":3,"sources":["webpack://./src/CustomMarker/CustomMarker.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,eAAe;EACf,iBAAiB;EACjB,qBAAqB;EACrB,YAAY;EACZ,kBAAkB;EAClB,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,WAAW;EACX,6CAA6C;EAC7C,kBAAkB;EAClB,kBAAkB;EAClB,YAAY;EACZ,SAAS;EACT;iBACe;EACf,WAAW;EACX,YAAY;EACZ,yCAAyC;EACzC,wBAAwB;EACxB,UAAU;AACZ;;AAEA;;EAEE,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,yBAAyB;EACzB,YAAY;AACd;;AAEA;;EAEE,kBAAkB;EAClB,OAAO;EACP,MAAM;EACN,QAAQ;EACR,SAAS;AACX;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,YAAY;EACZ,UAAU;EACV,kBAAkB;EAClB,aAAa;EACb,SAAS;EACT,QAAQ;EACR,mBAAmB;EACnB,WAAW;EACX,WAAW;EACX,kBAAkB;EAClB,mBAAmB;EACnB,kEAAkE;EAClE,+DAA+D;EAC/D,6DAA6D;EAC7D,8DAA8D;EAC9D;AACF;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,UAAU;AACZ;;AAEA;EACE,sBAAsB;EACtB,YAAY;EACZ,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,uBAAuB;EACvB,YAAY;EACZ,UAAU;EACV,kBAAkB;AACpB;;AAEA,UAAU;AACV;EACE,yBAAyB;AAC3B;AACA;EACE,kEAAkE;EAClE,+DAA+D;EAC/D,6DAA6D;EAC7D,8DAA8D;EAC9D,0DAA0D;EAC1D,uBAAuB;EACvB,2BAA2B;EAC3B;AACF;;AAEA;EACE,YAAY;EACZ,WAAW;EACX,QAAQ;AACV;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,sBAAsB;EACtB,uBAAuB;AACzB;;AAEA;EACE,aAAa;AACf","sourcesContent":[".custom-marker__container {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 10px;\n  line-height: 1.35;\n  letter-spacing: 0.6px;\n  color: white;\n  position: relative;\n  box-sizing: border-box;\n  top: -50%;\n}\n\n.custom-marker__point .custom-marker__bg-pointer {\n  content: \"\";\n  box-shadow: -2px -2px 2px 0px rgb(0 0 0 / 8%);\n  border-radius: 2px;\n  position: absolute;\n  bottom: -3px;\n  left: 50%;\n  /* width: 16px;\n  height: 16px; */\n  width: 26px;\n  height: 26px;\n  transform: translateX(-50%) rotate(45deg);\n  transform-origin: center;\n  z-index: 1;\n}\n\n.custom-marker__bg,\n.custom-marker__container {\n  min-width: 100%;\n  min-height: 100%;\n}\n\n.custom-marker__bg {\n  border-ra